import { localize } from './i18n'

export function notify(
    str: string,
    arg1?: 'warning' | 'info' | 'error' | boolean | Record<string, string>,
    arg2?: boolean | Record<string, string>,
    arg3?: boolean
) {
    const type = typeof arg1 === 'string' ? arg1 : 'info'
    const data = typeof arg1 === 'object' ? arg1 : typeof arg2 === 'object' ? arg2 : undefined
    const permanent = typeof arg1 === 'boolean' ? arg1 : typeof arg2 === 'boolean' ? arg2 : arg3 ?? false

    ui.notifications.notify(localize(str, data), type, { permanent })
}

export function warn(str: string, arg1?: boolean | Record<string, string>, arg2?: boolean) {
    notify(str, 'warning', arg1, arg2)
}

export function info(str: string, arg1?: boolean | Record<string, string>, arg2?: boolean) {
    notify(str, 'info', arg1, arg2)
}

export function error(str: string, arg1?: boolean | Record<string, string>, arg2?: boolean) {
    notify(str, 'error', arg1, arg2)
}
