import MODULE_ID from '~./src/module'

export function localize(key: string, data?: Record<string, string | number>) {
    key = `${MODULE_ID}.${key}`
    if (data) return game.i18n.format(key, data)
    return game.i18n.localize(key)
}

export function subLocalize(subKey: string) {
    const fn = (key: string, data?: Record<string, string | number>) => localize(`${subKey}.${key}`, data)
    Object.defineProperty(fn, 'key', {
        get() {
            return subKey
        },
        enumerable: false,
        configurable: false,
    })
    return fn as ((key: string, data?: Record<string, string | number>) => string) & { key: string }
}
