import MODULE_ID from '~./src/module'

export function hasModuleFlag(doc: foundry.abstract.Document) {
    // @ts-ignore
    return !!doc.flags && MODULE_ID in doc.flags
}

export function getFlag<T extends unknown>(doc: foundry.abstract.Document, key: string, ...keys: string[]) {
    keys.unshift(key)
    return doc.getFlag(MODULE_ID, keys.join('.')) as T | undefined
}

/**
 * @template {foundry.Document} T
 * @param {T} doc
 * @param {string} key
 * @param {any} value
 * @returns T
 */
export function setFlag(doc: foundry.abstract.Document, key: string, value: unknown) {
    return doc.setFlag(MODULE_ID, key, value)
}
